@import url(https://fonts.googleapis.com/css2?family=Mali:wght@400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700&display=swap);
@font-face{font-family:"icons";src:url(/testbeetroot/static/media/icons.b8c99b5f.ttf) format("truetype"),url(/testbeetroot/static/media/icons.b6aee4e1.woff) format("woff"),url(/testbeetroot/static/media/icons.62fe975a.svg) format("svg");font-weight:normal;font-style:normal;font-display:block}[class^="_icon-"]::before,[class*=" _icon-"]::before{font-family:"icons" !important;speak:never;font-style:normal;font-weight:normal;font-feature-settings:normal;font-variant:normal;text-transform:none;line-height:1;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}._icon-close-small:before{content:"\e900"}._icon-ok-small:before{content:"\e901"}

*{margin:0;padding:0;box-sizing:border-box}body{overflow:auto}

.me-day{position:relative;display:-webkit-flex;display:flex;width:100%;height:100%;position:relative;border-top:1px solid #cecdcd}.me-day.happy{border-top:2px solid #97ca4f}.me-day.sad{border-top:2px solid #e35d5a}.me-day__date,.me-day__update{color:#1D1B19;position:absolute;top:16px}.me-day__date{left:16px}.me-day__date.another{color:#818690}.me-day__date.today{color:#e35d5a;font-weight:700}.me-day__update{cursor:pointer;right:16px;color:transparent}.me-day__body{width:100%;height:100%;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;line-height:40px;overflow-x:auto}.me-day__body img{width:40px;height:40px}.me-day:hover{background-color:rgba(87,148,116,0.08)}.me-day:hover .me-day__update{color:#1D1B19}.me-day.sun{border-right:2px solid #E35D5A}.me-day.mon{border-right:2px solid #FF8E4E;border-left:none}.me-day.tue{border-right:2px solid #FDD66A;border-left:none}.me-day.wed{border-right:2px solid #91D195;border-left:none}.me-day.thu{border-right:2px solid #5B89DB;border-left:none}.me-day.fri{border-right:2px solid #D185F7;border-left:none}

.m-board{color:#818690;font-family:"Poppins",sans-serif;font-weight:400;font-size:14px;position:relative;display:grid;grid-template-rows:53px repeat(5, minmax(120px, 1fr));grid-template-columns:repeat(7, minmax(72px, 1fr));border-radius:10px;background-color:white;transition:1s}.m-board__weekdays{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center}.m-board.large{grid-template-rows:53px repeat(6, minmax(120px, 1fr))}

.me-popup{font-size:14px;font-color:#CFD5D7;font-weight:400;position:absolute;width:285px;height:192px;padding:28px;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:space-between;justify-content:space-between;background-color:white;border-radius:10px;box-shadow:1px 1px 5px rgba(0,0,0,0.5);z-index:20}.me-popup__line{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center}.me-popup__line span{margin-right:4px}.me-popup__line span.active{font-weight:800}.me-popup__line>img{height:16px;width:16px;margin-right:14px}

.m-editor{display:grid;width:424px;border-radius:10px;grid-template-rows:repeat(6, 56px) 72px;grid-template-columns:150px 1fr;background-color:#fff;overflow:hidden;-webkit-align-items:center;align-items:center;padding-top:20px;font-size:14px;font-family:"Poppins",sans-serif;font-weight:400}.m-editor>label,.m-editor__clear-button{color:#818690;padding-left:24px}.m-editor>.m-counter,.m-editor__date{position:relative;padding-right:24px;text-align:center;line-height:40px;color:#1D1B19}.m-editor>.m-counter-text,.m-editor__date-text{border-bottom:1px solid #cfd5d7;background:no-repeat right border-box url(/testbeetroot/static/media/me-calendar-ico.bf0dc8a4.svg);background-size:24px}.m-editor__clear-button{border:none;box-shadow:none;display:inline-block;outline:none;font:inherit;text-align:start;width:100%;background-color:transparent;transition:all 0.2s ease;text-decoration:underline;cursor:pointer}.m-editor__clear-button:hover:not([disabled]){-webkit-transform:scale(1.1);transform:scale(1.1)}.m-editor__clear-button:disabled{color:#CFD5D7;cursor:default}.m-editor__control{align-self:end;display:-webkit-flex;display:flex;grid-column-gap:1px;-webkit-column-gap:1px;column-gap:1px;-webkit-justify-content:stretch;justify-content:stretch;height:56px;width:100%;grid-column:1/3;background-color:#CFD5D7;border-bottom-left-radius:10px;border-bottom-right-radius:10px;box-shadow:0 5px 15px rgba(0,0,0,0.295);overflow:hidden}.m-editor__control>button{border:none;box-shadow:none;display:inline-block;outline:none;font:inherit;text-align:start;background-color:#fff;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;-webkit-flex:1 1 auto;flex:1 1 auto;font-size:14px}.m-editor__control>button::before{font-size:24px}.m-editor__control>button:hover:not([disabled]){background-color:rgba(87,148,116,0.08)}.m-editor__control>button.m-cancel{color:#E35D5A}.m-editor__control>button.m-save{color:#396A61}.m-editor__control>button.m-save:disabled{color:#CFD5D7}

.m-counter__wrapper{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:space-between;justify-content:space-between;border-bottom:1px solid #cfd5d7;height:40px}.m-counter__input{border:none;box-shadow:none;display:inline-block;outline:none;font:inherit;text-align:center;width:100%;background-color:transparent}.m-counter>*>img{cursor:pointer;height:24px;width:24px;transition:all 0.2s ease}.m-counter>*>img:hover{-webkit-transform:scale(1.2);transform:scale(1.2)}input::-webkit-outer-spin-button,input::-webkit-inner-spin-button{-webkit-appearance:none;margin:0}input[type="number"]{-moz-appearance:textfield}

.m-status{display:-webkit-flex;display:flex;grid-column-gap:16px;-webkit-column-gap:16px;column-gap:16px;-webkit-align-items:center;align-items:center}.m-status .m-status__line{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;font-size:1.5rem;height:40px;width:40px}.m-status .m-status__line.active{border:1px solid #396A61;border-radius:50%}

.m-header{display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between;-webkit-align-items:center;align-items:center;height:144px;overflow-x:auto}.m-header .m-wrapper{display:-webkit-flex;display:flex;grid-column-gap:0.9rem;-webkit-column-gap:0.9rem;column-gap:0.9rem}

.m-month{font-family:"Poppins",sans-serif;font-weight:400;font-size:24px;display:-webkit-flex;display:flex;grid-column-gap:0.5rem;-webkit-column-gap:0.5rem;column-gap:0.5rem;-webkit-align-items:center;align-items:center;-webkit-justify-content:space-between;justify-content:space-between;width:270px}.m-month__next{font:inherit;cursor:pointer;border:none;box-shadow:none;display:inline-block;outline:none;margin:0;padding:0;background:transparent;transition:all 0.3s ease}.m-month__next:hover{-webkit-transform:scale(1.2);transform:scale(1.2)}.m-month__name{text-align:center}

.me-header-button{font-family:"Poppins",sans-serif;font-size:14px;font-weight:500;display:-webkit-flex;display:flex;height:64px;border-radius:32px;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center;grid-column-gap:10px;-webkit-column-gap:10px;column-gap:10px}.me-header-button>img{width:40px;height:40px;transition:all 0.3s ease}.me-header-button>img:hover{-webkit-transform:scale(1.2);transform:scale(1.2)}.me-header-button>img.grabbable{cursor:move;cursor:grab;cursor:-webkit-grab}.me-header-button.greenstar,.me-header-button.goldstar{width:110px;box-shadow:inset 3px 3px 4px #0000001e, 3px 3px 4px white}.me-header-button.help,.me-header-button.text{border:1px solid #cfd5d7}.me-header-button.help{width:64px}.me-header-button.text{width:92px;transition:all 0.3s ease}.me-header-button.text:hover{font-size:16.8px}

.me-help{font-family:"Poppins",sans-serif;background-color:#fff;max-width:344px;max-height:80vh;margin:24px 40px 0 40px;overflow:auto}.me-help__title{font-size:24px;font-weight:600;padding-bottom:30px}.me-help__section-title{font-size:24px;font-weight:400;padding-bottom:10px}.me-help__section-images{display:-webkit-flex;display:flex;grid-column-gap:30px;-webkit-column-gap:30px;column-gap:30px;-webkit-align-items:center;align-items:center;font-size:1rem;font-weight:500}.me-help__section-images>img{height:16px;width:16px}.me-help__section-text{color:#818690;font-size:14px;font-weight:400;padding-bottom:10px}.me-help *>img{height:16px;width:16px}.me-help *>img.greenstar{height:30px;width:30px}.me-help *>img.goldstar{height:38px;width:38px}

.m-modal{position:fixed;top:0;left:0;width:100%;height:100%;background:rgba(0,0,0,0.7);z-index:20}.m-modal__main{position:fixed;top:50%;left:50%;background-color:white;border-radius:10px;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%)}.m-modal__main>button{position:absolute;top:0.8rem;right:0.8rem;cursor:pointer;border:none;outline:none;margin:0;padding:0;background:transparent;font-size:1.5rem;color:grey;transition:all 0.3s ease}.m-modal__main>button:hover{-webkit-transform:scale(1.2);transform:scale(1.2)}.m-modal.show{display:block}.m-modal.hide{display:none}

.m-calendar{background-color:#F5F6F6;min-height:100vh;min-width:740px;margin:0 auto;cursor:pointer}.m-calendar .main-wrapper{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;max-width:1096px;min-width:740px;margin:0 auto}

